* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  background-color: #fff;
  margin: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  color: #000;
  text-decoration: none;
}

input[type="text"] {
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

button {
  cursor: pointer;
  padding: 10px 15px;
  font-size: 16px;
}

.timer {
  justify-content: space-around;
  margin-top: 20px;
  display: flex;
}

.field {
  text-align: center;
}

.value {
  color: #00f;
  font-size: 24px;
  font-weight: bold;
}

.label {
  color: gray;
  font-size: 14px;
}

.alert {
  color: #fff;
  text-align: center;
  background-color: red;
  margin-top: 10px;
  padding: 10px;
  display: none;
}

